.slider-pagination__button {
  border: none;
  width: 6px;
  height: 6px;
  padding: 0;
  background: @black-green;
  opacity: 0.3;
  border-radius: 50%;

  &:hover {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.1;
  }
}

.slider-pagination__button--current {
  width: 12px;
  height: 12px;
  padding: 0;
  background: @white;
  border: 3px solid @black-green;
  border-radius: 50%;
  opacity: 1;

  &:hover {
    opacity: 1;
  }

  &:active {
    opacity: 1;
  }
}
